"use client";
import React, { useEffect, useState } from "react";

export default function Preloader() {
  const [hydrated, setHydrated] = useState(false);
  useEffect(() => {
    setHydrated(true);
  }, []);
  if (hydrated) return null;
  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      width: '100vw',
      height: '100vh',
      background: '#18181b',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 9999,
      transition: 'opacity 0.3s',
    }}>
      <img src="/faviconn.png" alt="Loading..." style={{ width: 80, height: 80, objectFit: 'contain', filter: 'drop-shadow(0 2px 8px #0008)' }} />
    </div>
  );
} 